}

/* ===== MEDIA QUERIES (SM) ===== */
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        gap: .5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}