.exp__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.exp__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.exp__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.exp__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.exp__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.exp__details {
    display: flex;
    gap: 1rem;
}

.exp__details-icon {
    margin-top: 5px;
    color: var(--color-primary);
}

/* ===== MEDIA QUERIES (MD) ===== */
@media screen and (max-width: 1024px) {
    .exp__container {
        grid-template-columns: 1fr;
    }

    .exp__container > div {
        width: 90%;
        padding: 1.8rem;
        margin: 0 auto;
    }

    .exp__content {
        padding: 1rem;
    }
}

@media screen and (min-width: 600px) {
    
}

/* ===== MEDIA QUERIES (SM) ===== */
@media screen and (max-width: 600px) {
    .exp__container {
        display: grid;
        gap: 1rem;
        grid-template-columns: repeat(1, 1fr);
        place-items: center;
    }

    .exp__details {
        display: flex;
        gap: 1rem;
        font-size: .8em;
    }
    .exp__content {
        padding: .5rem;
        row-gap: .5rem;
        place-content: space-between;
        place-items: center;
    }
    .exp__container > div {
        width: 100%;
        padding: 1rem 1rem .4rem;
    }
    .exp__container > div h3 {
        margin-bottom: .5rem;
    }
}