.port__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  width: var(--container-width-lg);
  margin: 0 auto;
}

.port__item {
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.port__item:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
}

.port__item-image {
  border-radius: 1.5rem;
  overflow: hidden;
}

.port__item h3 {
  margin: 1.2rem 0 2rem;
}

.port__item-cta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

/* ===== MEDIA QUERIES (MD) ===== */
@media screen and (max-width: 1224px) {
  .port__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.2rem;
  }
}

@media screen and (min-width: 600px) {
  header {
    height: 100vh;
  }
}

/* ===== MEDIA QUERIES (SM) ===== */
@media screen and (max-width: 800px) {
  .port__container {
    grid-template-columns: 1fr 1fr;
    display: grid;
    gap: 1rem;
    place-content: center;
    place-items: center;
    width: var(--container-width-md);
    margin: 0 auto;

    .btn {
        padding: .3rem;
    }
    a {
        font-size: .7em;
    }

    .port__item-cta {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: .5rem;
        margin-bottom: .5rem;
    }
  }

  .port__item-image {
    border-radius: .5rem;
  }

  .port__item {
    padding: .8rem;
    border-radius: 1rem;
  }

  .port__item h3 {
    margin: .8rem 0;
    font-size: .55em;
  }
}
