header {
  height: 100vh;
  padding-top: 2rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ====== CTA ====== */
.cta {
  margin-top: 1rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ====== HEADER SOCIALS ====== */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;

  &::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
  }
}

/* ====== ME ====== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 50rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 1.5rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 0rem 0.5rem 0.5rem 0.5rem;
}

/* ====== SCROL DOWN ====== */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ===== MEDIA QUERIES (MD) ===== */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

@media screen and (min-width: 600px) {
  header {
    height: 100vh;
  }
}

/* ===== MEDIA QUERIES (SM) ===== */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;

    a {
        font-size: .8em;
      }

    h1 {
      font-size: 1.5em;
    }
    .cta {
      margin-top: 1rem;
      display: flex;
      gap: .9rem;
      justify-content: center;

      .btn {
        padding: 0.5em;
      }
    }
  }

  .header__socials,
  .scroll__down {
    display: none;
  }
}
